module.exports = "import React from 'react'\nimport { Field, reduxForm } from 'redux-form'\nimport normalizePhone from './normalizePhone'\n\nconst upper = value => value && value.toUpperCase()\nconst lower = value => value && value.toLowerCase()\nconst lessThan = otherField =>\n  (value, previousValue, allValues) => value < allValues[otherField] ? value : previousValue\nconst greaterThan = otherField =>\n  (value, previousValue, allValues) => value > allValues[otherField] ? value : previousValue\n\nconst FieldNormalizingForm = (props) => {\n  const { handleSubmit, pristine, reset, submitting } = props\n  return (\n    <form onSubmit={handleSubmit}>\n      <div>\n        <label>Username</label>\n        <div>\n          <Field\n            name=\"username\"\n            component=\"input\"\n            type=\"text\"\n            placeholder=\"Username\"\n            normalize={lower}\n          />\n        </div>\n      </div>\n      <div>\n        <label>Shout</label>\n        <div>\n          <Field\n            name=\"shout\"\n            component=\"input\"\n            type=\"text\"\n            placeholder=\"Shout something!\"\n            normalize={upper}\n          />\n        </div>\n      </div>\n      <div>\n        <label>Phone</label>\n        <div>\n          <Field\n            name=\"phone\"\n            component=\"input\"\n            type=\"text\"\n            placeholder=\"Phone Number\"\n            normalize={normalizePhone}\n          />\n        </div>\n      </div>\n      <div>\n        <label>Min</label>\n        <div>\n          <Field\n            name=\"min\"\n            component=\"input\"\n            type=\"number\"\n            normalize={lessThan('max')}\n          />\n        </div>\n      </div>\n      <div>\n        <label>Max</label>\n        <div>\n          <Field\n            name=\"max\"\n            component=\"input\"\n            type=\"number\"\n            normalize={greaterThan('min')}\n          />\n        </div>\n      </div>\n      <div>\n        <button type=\"submit\" disabled={submitting}>Submit</button>\n        <button type=\"button\" disabled={pristine || submitting} onClick={reset}>Clear Values\n        </button>\n      </div>\n    </form>\n  )\n}\n\nexport default reduxForm({\n  form: 'normalizing', // a unique identifier for this form\n  initialValues: { min: 1, max: 10 }\n})(FieldNormalizingForm)\n"


